/**
 * ============================================================================
 * MAIN CLASS
 * ============================================================================
 * @hidden
 */

/**
 * Defines [[Series]] for a candlestick graph.
 * 
 * @see {@link ICandlestickSeriesEvents} for a list of available Events
 * @see {@link ICandlestickSeriesAdapters} for a list of available Adapters
 * @todo Example
 * @important
 */
export class CandlestickSeries extends ColumnSeries {

	/**
	 * Defines available data fields.
	 *
	 * @ignore Exclude from docs
	 * @type {ICandlestickSeriesDataFields}
	 */
	public _dataFields: ICandlestickSeriesDataFields;

	/**
	 * Defines available properties.
	 *
	 * @ignore Exclude from docs
	 * @type {ICandlestickSeriesProperties}
	 */
	public _properties: ICandlestickSeriesProperties;

	/**
	 * Defines available adapters.
	 *
	 * @ignore Exclude from docs
	 * @type {ICandlestickSeriesAdapters}
	 */
	public _adapter: ICandlestickSeriesAdapters;

	/**
	 * Event dispacther.
	 * 
	 * @type {SpriteEventDispatcher<AMEvent<CandlestickSeries, ICandlestickSeriesEvents>>} Event dispatcher instance
	 * @todo Disabled to work around TS bug (see if we can re-enable it again)
	 */
	//public events: SpriteEventDispatcher<AMEvent<CandlestickSeries, CandlestickSeriesEvents>>;

	/**
	 * Defines the type of data item.
	 *
	 * @ignore Exclude from docs
	 * @type {CandlestickSeriesDataItem}
	 */
	public _dataItem: CandlestickSeriesDataItem;

	/**
	 * A data field to look for "low" value for horizontal axis.
	 */
	protected _xLowField: keyof this["_dataFields"];

	/**
	 * A data field to look for "low" value for vertical axis.
	 */
	protected _yLowField: keyof this["_dataFields"];

	/**
	 * A data field to look for "high" value for horizontal axis.
	 */
	protected _xHighField: keyof this["_dataFields"];

	/**
	 * A data field to look for "high" value for vertical axis.
	 */
	protected _yHighField: keyof this["_dataFields"];

	/**
	 * Internal iterator.
	 */
	protected _highLinesIterator: $iter.ListIterator<Line>;

	/**
	 * Internal iterator.
	 */
	protected _lowLinesIterator: $iter.ListIterator<Line>;

	/**
	 * List of "low" line elements.
	 * 
	 * @type {ListTemplate<Line>}
	 */
	protected _lowLines: ListTemplate<Line>;

	/**
	 * List of "high" line elements.
	 * 
	 * @type {ListTemplate<Line>}
	 */
	protected _highLines: ListTemplate<Line>;

	/**
	 * Constructor
	 */
	constructor() {
		super();
		this.className = "CandlestickSeries";

		this.lowLines;
		this.highLines;

		this.applyTheme();
	}

	/**
	 * Sets defaults that instantiate some objects that rely on parent, so they
	 * cannot be set in constructor.
	 */
	protected applyInternalDefaults(): void {
		super.applyInternalDefaults();
		this.readerTitle = this.language.translate("Candlestick Series");
	}

	/**
	 * Returns a new/empty DataItem of the type appropriate for this object.
	 * 
	 * @see {@link DataItem}
	 * @return {CandlestickSeriesDataItem} Data Item
	 */
	protected createDataItem(): this["_dataItem"] {
		return new CandlestickSeriesDataItem();
	}

	/**
	 * Validates data item's element, effectively redrawing it.
	 *
	 * @ignore Exclude from docs
	 * @param {CandlestickSeriesDataItem}  dataItem  Data item
	 */
	public validateDataElement(dataItem: this["_dataItem"]): void {
		super.validateDataElement(dataItem);
		let lowLine: Line = this._lowLinesIterator.getFirst();
		let highLine: Line = this._highLinesIterator.getFirst();

		lowLine.dataItem = dataItem;
		highLine.dataItem = dataItem;

		let column: Sprite = dataItem.column;

		if (this.baseAxis == this.xAxis) {
			let x: number = column.pixelX + column.pixelWidth / 2;
			lowLine.x = x;
			highLine.x = x;

			let open = dataItem.getWorkingValue(this.yOpenField);
			let close = dataItem.getWorkingValue(this.yField);

			let yOpen = this.yAxis.getY(dataItem, this.yOpenField);
			let yClose = this.yAxis.getY(dataItem, this.yField);
			let yLow = this.yAxis.getY(dataItem, this.yLowField);
			let yHigh = this.yAxis.getY(dataItem, this.yHighField);

			lowLine.y1 = yLow;
			highLine.y1 = yHigh;

			if (open < close) {
				lowLine.y2 = yOpen;
				highLine.y2 = yClose;
			}
			else {
				lowLine.y2 = yClose;
				highLine.y2 = yOpen;
			}

		}
		if (this.baseAxis == this.yAxis) {
			let y: number = column.pixelY + column.pixelHeight / 2;

			lowLine.y = y;
			highLine.y = y;

			let open = dataItem.getWorkingValue(this.xOpenField);
			let close = dataItem.getWorkingValue(this.xField);

			let xOpen = this.xAxis.getX(dataItem, this.xOpenField);
			let xClose = this.xAxis.getX(dataItem, this.xField);
			let xLow = this.xAxis.getX(dataItem, this.xLowField);
			let xHigh = this.xAxis.getX(dataItem, this.xHighField);

			lowLine.x1 = xLow;
			highLine.x1 = xHigh;

			if (open < close) {
				lowLine.x2 = xOpen;
				highLine.x2 = xClose;
			}
			else {
				lowLine.x2 = xClose;
				highLine.x2 = xOpen;
			}
		}

		this.setColumnStates(lowLine);
		this.setColumnStates(highLine);

		lowLine.parent = this.columnsContainer;
		highLine.parent = this.columnsContainer;
	}

	/**
	 * (Re)validates the whole series, effectively causing it to redraw.
	 *
	 * @ignore Exclude from docs
	 */
	public validate() {
		this._columnsIterator.reset();
		this._lowLinesIterator.reset();
		this._highLinesIterator.reset();
		super.validate();
	}

	/**
	 * A data field to look for "low" value for horizontal axis.
	 *
	 * @ignore Exclude from docs
	 * @return {string} Field name
	 */
	public get xLowField(): string {
		return this._xLowField;
	}

	/**
	 * A data field to look for "low" value for vertical axis.
	 *
	 * @ignore Exclude from docs
	 * @return {string} Field name
	 */
	public get yLowField(): string {
		return this._yLowField;
	}

	/**
	 * A data field to look for "high" value for horizontal axis.
	 *
	 * @ignore Exclude from docs
	 * @return {string} Field name
	 */
	public get xHighField(): string {
		return this._xHighField;
	}

	/**
	 * A data field to look for "high" value for vertical axis.
	 *
	 * @ignore Exclude from docs
	 * @return {string} Field name
	 */
	public get yHighField(): string {
		return this._yHighField;
	}

	/**
	 * Sets up which data fields to use for data access.
	 */
	protected defineFields() {
		super.defineFields();

		if (this.baseAxis == this.xAxis) {
			let yAxisFieldName: string = $utils.capitalize(this.yAxis.axisFieldName);
			this._yLowField = <keyof this["_dataFields"]>("low" + yAxisFieldName + "Y");
			this._yHighField = <keyof this["_dataFields"]>("high" + yAxisFieldName + "Y");
		}

		if (this.baseAxis == this.yAxis) {
			let xAxisFieldName: string = $utils.capitalize(this.xAxis.axisFieldName);
			this._xLowField = <keyof this["_dataFields"]>("low" + xAxisFieldName + "X");
			this._xHighField = <keyof this["_dataFields"]>("high" + xAxisFieldName + "X");
		}

		this.addValueField(this.xAxis, <any>this._xValueFields, <any>this._xLowField);
		this.addValueField(this.xAxis, <any>this._xValueFields, <any>this._xHighField);

		this.addValueField(this.yAxis, <any>this._yValueFields, <any>this._yLowField);
		this.addValueField(this.yAxis, <any>this._yValueFields, <any>this._yHighField);
	}

	/**
	 * List of "low" line elements.
	 *
	 * @ignore Exclude from docs
	 * @return {ListTemplate<Line>} Element list
	 */
	public get lowLines(): ListTemplate<Line> {
		if (!this._lowLines) {
			let lineTemplate: Line = new Line();
			lineTemplate.isMeasured = false;

			this._lowLines = new ListTemplate<Line>(lineTemplate);

			this._lowLinesIterator = new $iter.ListIterator<Line>(this._lowLines, () => this._lowLines.create());
			this._lowLinesIterator.createNewItems = true;
		}
		return this._lowLines;
	}

	/**
	 * List of "high" line elements.
	 *
	 * @ignore Exclude from docs
	 * @return {ListTemplate<Line>} Element list
	 */
	public get highLines(): ListTemplate<Line> {
		if (!this._highLines) {
			let lineTemplate: Line = new Line();
			lineTemplate.isMeasured = false;

			this._highLines = new ListTemplate<Line>(lineTemplate);

			this._highLinesIterator = new $iter.ListIterator<Line>(this._highLines, () => this._highLines.create());
			this._highLinesIterator.createNewItems = true;
		}
		return this._highLines;
	}

	/**
	 * Creates elements in related legend container, that mimics the look of this
	 * Series.
	 *
	 * @ignore Exclude from docs
	 * @param {Container}  marker  Legend item container
	 */
	public createLegendMarker(marker: Container): void {

		let w: number = marker.pixelWidth;
		let h: number = marker.pixelHeight;

		marker.removeChildren();

		let column: RoundedRectangle = marker.createChild(RoundedRectangle);
		column.copyFrom(<any>this.columns.template);

		let cw: number;
		let ch: number;

		let highLine: Line = marker.createChild(Line);
		let lowLine: Line = marker.createChild(Line);

		if (this.baseAxis == this.yAxis) {
			cw = w / 3;
			ch = h;

			highLine.y = h / 2;
			lowLine.y = h / 2;

			highLine.x2 = w / 3;
			lowLine.x2 = w / 3;
			lowLine.x = w / 3 * 2;
			column.x = w / 3;
		}
		else {
			cw = w;
			ch = h / 3;
			highLine.x = w / 2;
			lowLine.x = w / 2;

			highLine.y2 = h / 3;
			lowLine.y2 = h / 3;
			lowLine.y = h / 3 * 2;
			column.y = h / 3;
		}
		column.width = cw;
		column.height = ch;

		$object.copyProperties(this, marker, visualProperties);
		$object.copyProperties(this.columns.template, column, visualProperties);
		$object.copyProperties(this.lowLines.template, lowLine, visualProperties);
		$object.copyProperties(this.highLines.template, highLine, visualProperties);
	}

}

/**
 * Register class in system, so that it can be instantiated using its name from
 * anywhere.
 * 
 * @ignore
 */
system.registeredClasses["CandlestickSeries"] = CandlestickSeries;
system.registeredClasses["CandlestickSeriesDataItem"] = CandlestickSeriesDataItem;